html {
  height: 100%;
}
.ant-layout{    background-color: #fff !important;}
body {
  height: 100%;
margin: 0;
padding: 0;
background-color: #fff !important;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.ant-breadcrumb{
  padding: 16px 0px;
}

@media (min-width: 320px) { 
  .ant-layout-content {padding: 16px 0;}
  .ant-list-item-meta-avatar {margin: 8px 24px 8px 8px;}
  .ant-layout-header{padding: 0 25px; box-shadow: 0px 0px 10px rgba(0,0,0,.1);}
  .ant-skeleton{ padding: 0 1.5rem;}
  .ant-skeleton-paragraph{ margin: 12px 0 !important;}
  .ant-skeleton-paragraph li{ height: 12px !important;}
  .ant-skeleton-title{margin: 0 !important;}
  .ant-page-header{ padding: 0 25px !important;}
}

@media (min-width: 576px) {  
  .ant-layout-content {padding: 40px 50px;}
  .ant-layout-header{padding: 0 50px; box-shadow: 0px 0px 10px rgba(0,0,0,.1);}  
}

@media (min-width: 768px) {  
  .ant-layout-content {padding: 40px 50px;}
  .ant-layout-header{padding: 0 50px; box-shadow: 0px 0px 10px rgba(0,0,0,.1);}  
}

@media (min-width: 992px) {  
  .ant-layout-content {padding: 40px 50px;}
  .ant-layout-header{padding: 0 50px; box-shadow: 0px 0px 10px rgba(0,0,0,.1);}  
}

@media (min-width: 1200px) {  
  .ant-layout-content {padding: 40px 50px;}
  .ant-layout-header{padding: 0 50px; box-shadow: 0px 0px 10px rgba(0,0,0,.1);}  
}